import React, { useState, useEffect } from "react"; // state allows us to save information
import articles from "./article-content";
import ArticlesList from "../components/ArticlesList";
import NotFoundPage from "./NotFoundPage";
import CommentsList from "../components/CommentsList";
import UpvoteSection from "../components/UpvoteSection";
import AddComponentForm from "../components/AddCommentForm";

const ArticlePage = ({ match }) => {
  const name = match.params.name;
  const article = articles.find((article) => article.name === name);

  // use state on art info and the function set art info
  // in useState({}) the info inside is default data
  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

  // this happens on the mount and on change
  // changing the component inside the effect will constanly change it
  // fix is to tell it what to listen to
  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      setArticleInfo(body);
    };
    fetchData();
    // setArticleInfo({ upvotes: Math.ceil(Math.random() * 10) });
  }, [name]); // use effect when name changes (when changing between articles)

  if (!article) return <NotFoundPage />;
  const otherArts = articles.filter((art) => art.name !== name);

  return (
    <>
      <h1>{article.title}</h1>
      <UpvoteSection
        upvotes={articleInfo.upvotes}
        articleName={name}
        setArticleInfo={setArticleInfo}
      />
      {article.content.map((para, key) => (
        <p key={key}> {para}</p>
      ))}
      <CommentsList comments={articleInfo.comments} />
      <AddComponentForm articleName={name} setArticleInfo={setArticleInfo}/>
      <h3>Other Articles</h3>
      <ArticlesList articles={otherArts} />
    </>
  );
};

export default ArticlePage;
